<template>
  <div class="VisitorAppointmentBox">
    <div
      class="VisitorAppointment"
      v-show="VisitorAppointmentShow == '1'"
      ref="visitor"
    >
      <div class="VisitorAppointmentItem" v-show="showItem == 'MyAppointment'">
        <div class="appHomeTitBox">
          <div class="titleName">|&nbsp;&nbsp;基本信息</div>
          <div
            class="appointment"
            @click="(showItem = 'NewAppointment'), getVisitorList_()"
          >
            我的预约
          </div>
        </div>
        <div class="appHomeContent">
          <div class="contentItem">
            <div class="itemLabel">
              |&nbsp;&nbsp;
              <span class="redSpot"></span>
              预约时间
            </div>

            <div class="itemValue w77">
              <div class="block">
                <div class="AddDate" ref="TimeText" @click="showPopFn(1)">
                  点击添加时间
                </div>
              </div>
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">
              |&nbsp;&nbsp; <span class="redSpot"></span>预约场馆
            </div>
            <div class="itemValue valueBox">
              <van-radio-group
                v-if="publicityNames.filter((c) => c.isOpen != '1').length > 0"
                v-model="VisitorAppointment.appointmentVenue"
              >
                <van-radio
                  icon-size="4vw"
                  v-for="item in publicityNames.filter((c) => c.isOpen != '1')"
                  :key="item.uuid"
                  :name="item.uuid"
                  shape="square"
                  >{{ item.publicityName }}</van-radio
                >
              </van-radio-group>
              <div v-else>暂无可预约场馆</div>
            </div>
            <div class="RemainingNum">{{ RemainingNumberText }}</div>
          </div>
        </div>
        <div class="appHomeTitBox">
          <div class="titleName">|&nbsp;&nbsp;访客身份</div>
        </div>
        <div class="appHomeContent">
          <div class="contentItem">
            <div class="itemLabel">
              |&nbsp;&nbsp; <span class="redSpot"></span>访客类型
            </div>
            <div class="itemValue w77" @click="showPopFn(2)">
              <div class="AddDate" ref="visitorText">
                {{ visitortypeName }}
              </div>
            </div>
          </div>
          <div class="contentItem" v-if="VisitorAppointment.visitorType != '1'">
            <div class="itemLabel">
              |&nbsp;&nbsp; <span class="redSpot"></span>访客人数
            </div>
            <div class="itemValue w77">
              <input
                type="text"
                v-model="VisitorAppointment.peopleNumber"
                placeholder="请输入访客人数"
              />
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">
              |&nbsp;&nbsp; <span class="redSpot"></span>访客单位
            </div>

            <div class="itemValue w77">
              <input
                type="text"
                placeholder="请输入访客单位"
                v-model="VisitorAppointment.company"
              />
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">
              |&nbsp;&nbsp; <span class="redSpot"></span>联系人员
            </div>

            <div class="itemValue w77">
              <input
                type="text"
                placeholder="请输入联系人员"
                v-model="VisitorAppointment.contacts"
              />
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">
              |&nbsp;&nbsp; <span class="redSpot"></span>联系方式
            </div>

            <div class="itemValue w77">
              <input
                type="text"
                placeholder="请输入联系方式"
                v-model="VisitorAppointment.contactNumber"
              />
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">
              |&nbsp;&nbsp; <span class="redSpot"></span>身份证号
            </div>

            <div class="itemValue w77">
              <input
                type="text"
                placeholder="请输入身份证号"
                v-model="VisitorAppointment.idNumber"
              />
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">
              |&nbsp;&nbsp; <span class="redSpot"></span>所在城市
            </div>

            <div class="itemValue w77">
              <input
                type="text"
                placeholder="请输入所在城市"
                v-model="VisitorAppointment.city"
              />
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">
              |&nbsp;&nbsp; <span class="redSpot"></span>是否境外
            </div>

            <div class="itemValue w77" @click="showPopFn(3)">
              <div class="AddDate">
                {{ isOverseasName }}
              </div>
            </div>
          </div>
        </div>
        <div class="MandatoryBox">
          <div class="MandatoryBoxItem">
            <input type="checkbox" v-model="checkVal" name id />
            <div>
              我已阅读
              <span>《参观须知》</span>，并保证以上填写内容正确属实
            </div>
          </div>
          <ul class="color8">
            <li>安全提醒</li>
            <li>疫情期间请自觉遵守各项安全防控规定。参观前请出示健康码。</li>
          </ul>
        </div>
        <div class="SubmitBtn" @click="Submit">提交</div>
        <div v-if="mask" class="mask">
          <div class="MessageBox">
            <div class="MessageTop">
              <div>广西雅长兰科植物保护区欢迎您</div>
              <img
                @click="closeMask"
                src="@/assets/phone/AppVisitorHome/error.png"
                alt
              />
            </div>
            <div class="MessageBom">
              <div>您的预约信息已经提交，</div>
              <div>请等待保护区工作人员审批</div>
            </div>
          </div>
        </div>
      </div>
      <div class="VisitorAppointmentItem" v-show="showItem == 'NewAppointment'">
        <div class="ItemTop">
          <div @click="showItem = 'MyAppointment'">新增预约</div>
        </div>
        <div
          class="ItemContent"
          v-for="item in MyAppointmentData"
          :key="item.uuid"
          @click="GoMyAppointment(item)"
        >
          <ul>
            <li class="ICTime">{{ timeFormat(item.appointmentTime, true) }}</li>
            <li>{{ item.appointmentVenue }}</li>
            <li>
              <icon
                :name="getIconName(item.checkState)"
                :color="getIconColor(item.checkState)"
                size="5vw"
              />
              <div :style="{ color: getIconColor(item.checkState) }">
                {{ getIconText(item.checkState) }}
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="VisitorAppointment" v-show="VisitorAppointmentShow == '2'">
      <div class="VisitorAppointmentItem">
        <div class="appHomeTitBox">
          <div class="titleName">|&nbsp;&nbsp;基本信息</div>
          <div class="titleGreen" @click="myAppointment">我的预约</div>
        </div>
        <div class="appHomeContent">
          <div class="contentItem">
            <div class="itemLabel">|&nbsp;&nbsp;预约场馆</div>
            <div class="itemValue w77">
              <div class="AddDate">
                {{ MyAppointmentItem.appointmentVenue }}
              </div>
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">|&nbsp;&nbsp;预约时间</div>
            <div class="itemValue w77">
              <div class="AddDate">
                {{ timeFormat(MyAppointmentItem.appointmentTime) }}
              </div>
            </div>
          </div>
        </div>
        <div class="appHomeTitBox">
          <div class="titleName">|&nbsp;&nbsp;访客身份</div>
        </div>
        <div class="appHomeContent">
          <div class="contentItem">
            <div class="itemLabel">|&nbsp;&nbsp;访客类型</div>
            <div class="itemValue input2">
              <div class="AddDate">
                {{
                  MyAppointmentItem.visitorType
                    ? getDictLabel(
                        MyAppointmentItem.visitorType,
                        "visitor_type"
                      )
                    : ""
                }}
              </div>
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">|&nbsp;&nbsp;访客单位</div>
            <div class="itemValue w77">{{ MyAppointmentItem.company }}</div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">|&nbsp;&nbsp;联系人员</div>
            <div class="itemValue w77">{{ MyAppointmentItem.contacts }}</div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">|&nbsp;&nbsp;联系方式</div>
            <div class="itemValue w77">
              {{ MyAppointmentItem.contactNumber }}
            </div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">|&nbsp;&nbsp;身份证号</div>
            <div class="itemValue w77">{{ MyAppointmentItem.idNumber }}</div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">|&nbsp;&nbsp;所在城市</div>
            <div class="itemValue w77">{{ MyAppointmentItem.city }}</div>
          </div>
          <div class="contentItem">
            <div class="itemLabel">|&nbsp;&nbsp;是否境外</div>
            <div class="itemValue w77 AddDate">
              <div class="AddDate">
                {{
                  MyAppointmentItem.isOverseas
                    ? getDictLabel(
                        MyAppointmentItem.isOverseas,
                        "isOverseasList"
                      )
                    : ""
                }}
              </div>
            </div>
          </div>
        </div>
        <div class="SubmitBtn mma" v-if="MyAppointmentItem.checkState == '2'">
          您的预约已通过
        </div>
        <div
          class="SubmitBtn mma"
          v-if="MyAppointmentItem.checkState == '3'"
          :style="{ background: '#ff5d00' }"
        >
          您的预约已取消
        </div>
        <div
          class="SubmitBtn mma"
          v-if="MyAppointmentItem.checkState == '1'"
          :style="{ background: '#FF900A' }"
        >
          您的预约正在处理中
        </div>
      </div>
    </div>
    <van-popup
      v-model="AddDateFlag"
      position="bottom"
      :style="{ height: '40%' }"
    >
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        title="选择完整时间"
        :min-date="minDate"
        @change="changeFn()"
        @confirm="confirmFn()"
        @cancel="cancelFn()"
      />
    </van-popup>
    <van-popup
      v-model="visitor_typeFlag"
      position="bottom"
      :style="{ height: '40%' }"
    >
      <van-picker
        :title="pickerName"
        show-toolbar
        :columns="pickerList"
        :value-key="optionsField.text"
        :columns-field-names="optionsField"
        @confirm="onConfirm"
        @cancel="onCancel"
      />
    </van-popup>
  </div>
</template>

<script>
import { addVisitor } from "@/api/case/case";
import store from "@/store";
import { facilitieslist } from "@/api/case/board";
import { getVisitorPeopleNumber, getVisitorList } from "@/api/data/reservation";
import {
  validatorPhone,
  validatorIDcard,
  positiveIntegerNumber,
} from "@/utils/validate";
import { Icon } from "vant";
// import { userParkDeptList } from "@/api/user";
export default {
  components: {
    Icon,
  },
  data() {
    return {
      VisitorAppointmentShow: "1",
      optionsField: {
        text: "dictLabel",
        values: "dictValue",
      },
      pickerList: [],
      pickerName: "",
      showPickType: "",
      isOverseasList: [], //是否境外
      visitor_type: [], //访客类型
      visitortypeName: "",
      isOverseasName: "",
      visitor_typeFlag: false,
      AddDateFlag: false,
      value1: "",
      minDate: new Date(),
      currentDate: new Date(),
      sheetVisible: false,
      stationList: [],
      stationObj: {},
      showItem: "MyAppointment",
      mask: false,
      checkVal: false,
      VisitorAppointment: {
        peopleNumber: "1",
        appointmentVenue: "",
        appointmentTime: "",
        visitorType: "",
        company: "",
        contacts: "",
        contactNumber: "",
        idNumber: "",
        city: "",
        isOverseas: "",
        visitStatus: "1",
        visitorId:
          window.sessionStorage.getItem("SET_TOKEN") || store.state.user.token,
      },
      searchObj: {
        startTime: "",
        endTime: "",
        manageStation: "",
      },

      publicityNames: [],
      yearNow: new Date().getFullYear(),
      monthNow: new Date().getMonth() + 1,
      timeType: "",
      value: "",
      MyAppointmentData: [],
      MyAppointmentItem: {},
      RemainingNumberText: "",
      RemainingNumber: "",
    };
  },
  computed: {
    numFn() {
      const { appointmentTime, appointmentVenue } =
        this.$data.VisitorAppointment;
      return {
        appointmentTime,
        appointmentVenue,
      };
    },
    ShowValue() {
      return this.$store.state.VisitorShowVal;
    },
  },
  watch: {
    "VisitorAppointment.visitorType"(val) {
      this.visitortypeName = this.getDictLabel(val, "visitor_type");
      if (val == 1) {
        this.VisitorAppointment.peopleNumber = 1;
      }
    },
    "VisitorAppointment.isOverseas"(val) {
      this.isOverseasName = this.getDictLabel(val, "isOverseasList");
    },
    numFn(val) {
      if (val.appointmentTime != "" && val.appointmentVenue != "") {
        let obj = {
          time: this.VisitorAppointment.appointmentTime,
          id: this.VisitorAppointment.appointmentVenue,
        };
        getVisitorPeopleNumber(obj).then((res) => {
          this.RemainingNumber = res.data;
          this.publicityNames.map((item) => {
            if (item.uuid == this.VisitorAppointment.appointmentVenue) {
              this.RemainingNumberText =
                item.publicityName + " 预约剩余名额:" + res.data + "人";
            }
          });
        });
      }
      deep: true;
    },
    ShowValue(val) {
      this.VisitorAppointmentShow = val;
    },
  },
  mounted: async function () {
    let _this = this;
    this.stationList.push({
      name: "全部",
      id: "",
      method: function (item) {
        _this.stationObj = { name: "全部", id: "" };
      },
    });
    facilitieslist().then((res) => {
      _this.publicityNames = res.data;
    });

    this.timeFormat(new Date());
  },
  created: async function () {
    let _this = this;
    await this.dictType("visitor_type").then((res) => {
      _this.visitor_type = res;
      if (!_this.VisitorAppointment.uuid) {
        _this.VisitorAppointment.visitorType = _this.visitor_type[0].dictValue;
      }
    });
    await this.dictType("is_electronic").then((res) => {
      _this.isOverseasList = res;
      if (!_this.VisitorAppointment.uuid) {
        _this.VisitorAppointment.isOverseas = _this.isOverseasList[1].dictValue;
      }
    });
  },
  methods: {
    getIconName(val) {
      let name = "passed";
      if (val == 1) {
        name = "more-o";
      } else if (val == 2) {
        name = "passed";
      } else {
        name = "close";
      }
      return name;
    },
    getIconColor(val) {
      let color = "#15CFA4";
      if (val == 1) {
        color = "#FF900A";
      } else if (val == 2) {
        color = "#15CFA4";
      } else {
        color = "#ff5d00";
      }
      return color;
    },
    getIconText(val) {
      let text = "预约成功";
      if (val == 1) {
        text = "处理中";
      } else if (val == 2) {
        text = "预约成功";
      } else {
        text = "预约失败";
      }
      return text;
    },
    getVisitorList_() {
      let obj = {
        visitorId:
          window.sessionStorage.getItem("SET_TOKEN") || store.state.user.token,
      };
      getVisitorList(obj).then((res) => {
        res.data.map((item) => {
          this.publicityNames.map((val) => {
            if (item.appointmentVenue == val.uuid) {
              item.appointmentVenue = val.publicityName;
            }
          });
        });
        this.MyAppointmentData = res.data;
      });
    },
    getDictLabel(val, name) {
      if (val) {
        if (this[name].find((c) => c.dictValue == val)) {
          return this[name].find((c) => c.dictValue == val).dictLabel;
        } else {
          return "";
        }
      } else {
        return "";
      }
    },
    Submit() {
      if (!this.checkVal) {
        this.$message.warning("请勾选参观须知");
      } else {
        let val = this.VisitorAppointment;
        if (
          val.peopleNumber != "" &&
          val.appointmentVenue != "" &&
          val.appointmentTime != "" &&
          val.visitorType != "" &&
          val.company != "" &&
          val.contacts != "" &&
          val.contactNumber != "" &&
          val.idNumber != "" &&
          val.city != "" &&
          val.visitStatus != "" &&
          val.isOverseas != ""
        ) {
          if (positiveIntegerNumber(val.peopleNumber).flag) {
            if (val.peopleNumber >= "1") {
              let phone =
                /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[189]))\d{8}$/;
              if (phone.test(val.contactNumber)) {
                let sf =
                  /^\d{6}((((((19|20)\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|(((19|20)\d{2})(0[13578]|1[02])31)|((19|20)\d{2})02(0[1-9]|1\d|2[0-8])|((((19|20)([13579][26]|[2468][048]|0[48]))|(2000))0229))\d{3})|((((\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|((\d{2})(0[13578]|1[02])31)|((\d{2})02(0[1-9]|1\d|2[0-8]))|(([13579][26]|[2468][048]|0[048])0229))\d{2}))(\d|X|x)$/;
                if (sf.test(val.idNumber)) {
                  if (this.RemainingNumber < 1) {
                    this.$message.error("预约人数已满");
                    return;
                  } else {
                    addVisitor(this.VisitorAppointment).then((res) => {
                      if (res.data.code == "200") {
                        this.mask = true;
                        this.VisitorAppointment =
                          this.$options.data().VisitorAppointment;
                        this.VisitorAppointment.visitorType =
                          this.visitor_type[0].dictValue;
                        this.VisitorAppointment.isOverseas =
                          this.isOverseasList[1].dictValue;
                        this.$refs.TimeText.innerText = "点击添加时间";
                        this.RemainingNumberText = "";
                      } else {
                        this.$message.warning(res.data.msg);
                      }
                    });
                  }
                } else {
                  this.$message.warning("身份证不正确");
                }
              } else {
                this.$message.warning("手机号不正确");
              }
            } else {
              this.$message.warning("请填写不人数,不小于1");
            }
          } else {
            this.$message.warning("请输入访客人数");
          }
        } else {
          this.$message.warning("请填写完整数据!");
        }
      }
    },
    closeMask() {
      this.mask = false;
      this.$refs.visitor.style = "overflow-y: scroll";
    },
    selectYear(str) {
      this.$refs[str].open();
    },
    showPopFn(flag) {
      this.showPickType = flag;
      if (flag == "1") {
        this.AddDateFlag = true;
      } else if (flag == "2") {
        this.visitor_typeFlag = true;
        this.pickerList = this.visitor_type;
        this.pickerName = "访客类型";
      } else if (flag == "3") {
        this.visitor_typeFlag = true;
        this.pickerList = this.isOverseasList;
        this.pickerName = "是否境外";
      }
    },

    changeFn() {
      // 值变化是触发
      this.changeDate = this.timeFormat(this.currentDate); // Tue Sep 08 2020 00:00:00 GMT+0800 (中国标准时间)
    },
    confirmFn() {
      // 确定按钮
      let val = this.timeFormat(this.currentDate);
      this.VisitorAppointment.appointmentTime = val;
      this.$refs.TimeText.innerText = val;
      this.AddDateFlag = false;
    },
    cancelFn() {
      this.AddDateFlag = false;
    },
    timeFormat(dateData, flag = false) {
      var date = new Date(dateData);
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      h = h < 10 ? "0" + h : h;
      var mi = date.getMinutes();
      mi = mi < 10 ? "0" + mi : mi;
      let time = y + "-" + m + "-" + d + " " + h + ":" + mi;
      if (flag) {
        time = y + "/" + m + "/" + d;
      }
      return time;
    },
    onConfirm(value, index) {
      if (this.showPickType == "1") {
        this.AddDateFlag = false;
      } else if (this.showPickType == "2") {
        this.visitor_typeFlag = false;
        this.VisitorAppointment.visitorType = value.dictValue;
      } else {
        this.visitor_typeFlag = false;
        this.VisitorAppointment.isOverseas = value.dictValue;
      }
    },
    onCancel() {
      if (this.visitor_typeFlag) {
        this.visitor_typeFlag = false;
      }
    },
    GoMyAppointment(item) {
      this.publicityNames.map((val) => {
        if (item.appointmentVenue == val.uuid) {
          item.appointmentVenue = val.publicityName;
        }
      });
      this.VisitorAppointmentShow = "2";
      this.$store.commit("SetVisitorShowVal", {
        VisitorShowVal: this.VisitorAppointmentShow,
      });
      this.MyAppointmentItem = item;
    },
    myAppointment() {
      this.VisitorAppointmentShow = "1";
      this.$store.commit("SetVisitorShowVal", {
        VisitorShowVal: this.VisitorAppointmentShow,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.VisitorAppointmentBox {
  .VisitorAppointment {
    width: 100%;
    height: 66.6vh;
    overflow: scroll;
    .VisitorAppointmentItem {
      width: 97vw;
      height: 66.6vh;
      margin: 0 auto;
      padding-bottom: 10vw;
      .appHomeTitBox {
        // height: 6.55vw;
        // line-height: 6.55vw;
        margin: 2vw 0 0 1vw;
        font-size: 4vw;
        // padding-top: 1vw;
        display: flex;
        justify-content: space-between;
        .titleName {
          font-size: 5vw;
          font-weight: bold;
        }
        .titleGreen {
          width: 23%;
          height: 6vw;
          line-height: 6vw;
          text-align: center;
          color: white;
          background-color: #4ecea3;
        }
        .appointment {
          width: 20vw;
          text-align: center;
          border-radius: 0.3vw;
          margin-right: 1vw;
          color: #ffffff;
          background-color: #20cfa4;
        }
      }
      .appHomeContent {
        margin-top: 2.6vw;
        width: calc(100% - 7.6vw);
        margin: 2.6vw 3.8vw 0;
        display: flex;
