/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUsername(str) {
  const valid_map = ['admin', 'editor']
  return valid_map.indexOf(str.trim()) >= 0
}

/**
 * @param {string} value
 * @returns {Boolean}
 */
export function validatorPhone(value) {
  if (value === '' || value == null) {
    return { flag: false, message: "手机号不能为空!" };
  } else if (!/^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[189]))\d{8}$/.test(value)) {
    return { flag: false, message: "手机号码格式错误!" };
  } else {
    return { flag: true, message: "" };
  }
}

/**
 * @param {string} value
 * @returns {Boolean}
 */
export function validatorIDcard(value) {
  if (value === '' || value == null) {
    return { flag: false, message: "身份证号不能为空!" };
  } else if (!/^\d{6}((((((19|20)\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|(((19|20)\d{2})(0[13578]|1[02])31)|((19|20)\d{2})02(0[1-9]|1\d|2[0-8])|((((19|20)([13579][26]|[2468][048]|0[48]))|(2000))0229))\d{3})|((((\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|((\d{2})(0[13578]|1[02])31)|((\d{2})02(0[1-9]|1\d|2[0-8]))|(([13579][26]|[2468][048]|0[048])0229))\d{2}))(\d|X|x)$/.test(value)) {
    return { flag: false, message: "身份证号格式错误!" };
  } else {
    return { flag: true, message: "" };
  }
}

/**
 * @param {string} value
 * @returns {Boolean}
 * @name 手机号验证
 */
export function validatorFormPhone(rule, value, callback) {
  if (value === '' || value == null) {
    callback()
  } else if (!/^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/.test(value)) {
    callback(new Error('手机号格式错误'))
  } else {
    callback()
  }
}

/**
 * @param {string} value
 * @returns {Boolean}
 */
export function positiveIntegerNumber(value) {
  if (value === '' || value == null) {
    return { flag: false, message: "请输入正整数!" };
  } else if (!/^[+]?(([1-9]\d*))?$/g.test(value)) {
    return { flag: false, message: "请输入正整数!" };
  } else {
    return { flag: true, message: "" };
  }
}

/**
 * @param {string} value
 * @returns {Boolean}
 * @name 密码强度验证
 */
export function cryptographicStrength(value) {
  if (value === '' || value == null) {
    return { flag: false, message: "请输入密码!" };
  } else if (!/^(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]*).{8,16}$/.test(value)) {
    return { flag: false, message: "密码须包含字母和数字,长度在8-16之间!" };
  } else {
    return { flag: true, message: "" };
  }
}


